<template>
  <div class="container">
    <IndexNavbar/>
    <div class="my-5 row justify-content-center">
      <form class="col-md-6" @submit.prevent="createOrder">
        <div class="form-group">
          <label for="useremail">Email</label>
          <input
            type="email"
            class="form-control"
            name="email"
            id="useremail"
            v-validate="'required|email'"
            v-model="form.user.email"
            placeholder=" Email"
          />
          <span class="text-danger" v-if="errors.has('email')">{{errors.first('email')}}</span>
        </div>

        <div class="form-group">
          <label for="username"></label>
          <input
            type="text"
            class="form-control"
            name="name"
            id="username"
            :class="{'is-invalid': errors.has('name')}"
            v-model="form.user.name"
            placeholder=""
            v-validate="'required'"
          />
          <span class="text-danger" v-if="errors.has('name')"></span>
        </div>

        <div class="form-group">
          <label for="usertel"></label>
          <input
            type="tel"
            class="form-control"
            name="tel"
            id="usertel"
            v-model="form.user.tel"
            placeholder=""
            v-validate="'required'"
          />
          <span class="text-danger" v-if="errors.has('tel')"></span>
        </div>

        <div class="form-group">
          <label for="useraddress"></label>
          <input
            type="text"
            class="form-control"
            name="address"
            id="useraddress"
            v-model="form.user.address"
            placeholder=""
            v-validate="'required'"
          />
          <span class="text-danger" v-if="errors.has('address')"></span>
        </div>

        <div class="form-group">
          <label for="comment"></label>
          <textarea
            name
            id="comment"
            class="form-control"
            cols="30"
            rows="10"
            v-model="form.message"
          ></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-danger"></button>
        </div>
      </form>
    </div>
    <IndexFooter/>
  </div>
</template>

<script>
import IndexNavbar from "./IndexNavbar";
import IndexFooter from "./IndexFooter";

export default {
  components: {
    IndexNavbar,
    IndexFooter
  },
  data () {
    return {
      form: {
        user: {
          name: '',
          email: '',
          tel: '',
          address: ''
        },
        message: ''
      }
    }
  },
  methods: {
    createOrder() {
      const url = `${process.env.APIPATH}/api/${process.env.CUSTOMPATH}/order`
      const order = this.form
      // this.isLoading = true
      this.$validator.validate().then(valid => {
        if (valid) {
          this.$http.post(url, {data: order}).then(response => {
            console.log('')
            if (response.data.success) {
              this.$router.push(`/customer_checkOut/${response.data.orderId}`)
            }
            this.isLoading = false
          })
        } else {
          console.log('')
        }
      })
    }
  }
}
</script>


